import React from "react";
import { Link } from "../../routes";
import Wrapper from "./Wrapper";

const PostItem = ({ post }) => (
  <Wrapper>
    <Link route="post" params={{ slug: post.slug }}>
      <h3>{post.name}</h3>
      <p>{post.body}</p>
    </Link>
  </Wrapper>
);

export default PostItem;
